<template>
  <div class="card cardItem p-2 mt-3">
    <div class="Item">
      <div class="card p-3 checkbox">
        <input type="checkbox" v-model="checked" @change="saveCheckedState" />
      </div>
      <div class="card p-3 name">
        <h3>{{ habit.name }}</h3>
        <i class="bi bi-lightning"></i>
      </div>
      <div class="card p-3 description">
        <p style="font-size: 1.2rem;">{{ habit.description }}</p>
        <p style="color: #2dce89"><strong>Goal :</strong> {{ habit.goal }}</p>
        <p style="color: #f72d66"><strong>Start :</strong> {{ habit.startDate }}</p>
      </div>
      <div class="card p-3 edit">
        <button style="width: 80% !important; border-radius:10rem;" class="btn btn-primary">
          <router-link :to="{ name: 'HabitDetail', params: { habitId: habit.id } }" style="text-decoration: none; color: white;">
            <i class="bi bi-pencil"></i>
          </router-link>
        </button>
      </div>
    </div>

    <div class="detail" v-if="isEditingLocal">
      <div :class="['update',{ visible: isUpdateVisible }]">
        <div class="addHabitNav">
          <h4>Update Habit</h4>
          <span id="closeBtn" @click="closeBtn" class="closeBtn">X</span>
        </div>
        <input style="margin-top: 1rem;" v-model="updatedHabit.name" class="form-control mb-4" placeholder="Alışkanlık Adı" />
        <input v-model="updatedHabit.description" class="form-control mb-4" placeholder="Açıklama" />
        <input v-model="updatedHabit.startDate" class="form-control mb-4" type="date" placeholder="Başlangıç Tarihi" />
        <input v-model="updatedHabit.goal" class="form-control mb-4" placeholder="Goal" />
        <button style="width: 100%;" @click="saveEdit" class="btn btn-success">Save</button>
      </div>
      <div class="butons">
        <button style="width:48%;"  @click="openUpdate" class="btn btn-primary ">Update</button>
        <button style="width:48%;" @click="deleteHabit" class="btn btn-danger ">Delete</button>
      </div>
    </div>
  </div>
</template>

<script>
import { useHabitStore } from '../stores/habitStore';
import { ref, onMounted, toRefs } from 'vue';
import { useRouter } from 'vue-router';

export default {
  props: {
    habit: Object,
    isEditing: {
      type: Boolean,
      default: false,
    },
  },
  setup(props) {
    const habitStore = useHabitStore();
    const isEditingLocal = ref(props.isEditing);
    const updatedHabit = ref({ ...props.habit });
    const checked = ref(false);
    const router = useRouter(); // router nesnesini oluşturun
  
    const isUpdateVisible = ref(false); // Başlangıçta görünmez

    const openUpdate = () => {
      isUpdateVisible.value = !isUpdateVisible.value; // Değişkeni tersine çevir
    };
    const closeBtn = () => {
      isUpdateVisible.value = !isUpdateVisible.value; // Değişkeni tersine çevir
    };

    const saveEdit = () => {
      habitStore.updateHabit(updatedHabit.value);
      isEditingLocal.value = false;
      router.push({ name: 'Home' }); 
    };
    
    const deleteHabit = () => {
      habitStore.deleteHabit(props.habit.id);
      router.push({ name: 'Home' }); 
    };

    const saveCheckedState = () => {
      habitStore.saveCheckedState(`habit-${props.habit.id}-checked`, checked.value);
    };

    const loadCheckedState = () => {
      checked.value = habitStore.loadCheckedState(`habit-${props.habit.id}-checked`);
    };

    onMounted(() => {
      loadCheckedState();
    });

    return {
      ...toRefs(props),
      isEditingLocal,
      updatedHabit,
      checked,
      isUpdateVisible,
      openUpdate,
      saveEdit,
      deleteHabit,
      saveCheckedState,
      closeBtn
    };
  },
};
</script>
