<!-- src/views/HabitList.vue -->
<template>
  <!-- Alışkanlık Ekleme Formu -->
 <div class="Habit">
   <div :class="['addHabit', 'card', 'p-3', 'mb-3', { visible: isAddHabitVisible }]" id="addHabit">
     <div class="addHabitNav">
       <h4>Add New Habits</h4>
       <span id="closeBtn" @click="closeBtn" class="closeBtn">X</span>
     </div>
     <div class="mb-4 mt-4" >
       <input type="text" v-model="newHabit.name" placeholder="Habit Name" class="form-control" required />
     </div>
     <div class="mb-4">
       <input type="text" v-model="newHabit.description" placeholder="Description" class="form-control"/>
     </div>
     <div class="mb-4">
       <input type="date" v-model="newHabit.startDate" placeholder="Start Date" class="form-control" required/>
     </div>
     <div class="mb-4">
       <input type="text" v-model="newHabit.goal" placeholder="Goal" class="form-control" />
     </div>
     <button @click="addNewHabit" class="btn btn-primary">Add Habit</button>
   </div>
 <div class="HabitList card mx-auto p-3">
   <!-- Alışkanlık Listesi -->
   <div class="row align-items-center">
     <h2>Habit List</h2>
     <button id="toggleAddHabit" @click="toggleAddHabit" class="btn btn-primary">Add Habits</button>
     <div v-for="habit in habits" :key="habit.id" class="col-12 mb-2">
      <HabitItem :habit="habit" />
     </div>
   </div>
 </div>
 </div>
</template>

<script>
import { useHabitStore } from '../stores/habitStore';
import { ref, computed } from 'vue';
import HabitItem from './HabitItem.vue';


// import jQuery from 'jquery';

export default {
 components: {HabitItem },
 setup() {
   const isAddHabitVisible = ref(false); // Başlangıçta görünmez
   const toggleAddHabit = () => {
   
     isAddHabitVisible.value = !isAddHabitVisible.value; // Değişkeni tersine çevir
   };
   const closeBtn = () => {
     isAddHabitVisible.value = !isAddHabitVisible.value; // Değişkeni tersine çevir
   };

   const habitStore = useHabitStore();
   const habits = computed(() => habitStore.habits); // Reaktif hale getiriyoruz
   habitStore.loadHabits();
   const newHabit = ref({
     id: null,
     name: '',
     description: '',
     startDate: '',
     goal: ''
   });

   const addNewHabit = () => {
     if (newHabit.value.name) {
       const habit = { ...newHabit.value, id: Date.now() };
       habitStore.addHabit(habit);
       newHabit.value = { id: null, name: '', description: '', startDate: '', goal: '' };
     }
   };

   return { 
     habits, newHabit, addNewHabit, isAddHabitVisible,
     toggleAddHabit,closeBtn,
   };
 },

};
</script>


