<!-- src/App.vue -->
<template>
  <div id="app">
    <Navbar/>
    <div class="container">
      <router-view />
    </div>
  </div>
</template>

<script>

import Navbar from './components/Navbar.vue';
import './style.css';
import jQuery from 'jquery';



export default {
  name: 'App',
  components: {
    Navbar,
    
  },
  mounted(){
      // Sayfa Yüklenince Tema Yükle
    const savedTheme = localStorage.getItem("theme") || "light";
    jQuery("html").attr("data-theme", savedTheme);

    // İkonu Güncelleme
    jQuery("#theme")
        .removeClass("bi bi-sun bi-moon")
        .addClass(savedTheme === "dark" ? "bi bi-sun" : "bi bi-moon");

    // Tema Geçiş Fonksiyonu
    jQuery("#theme-icons").on("click", function () {
        const currentTheme = jQuery("html").attr("data-theme");
        const newTheme = currentTheme === "dark" ? "light" : "dark";
        jQuery("html").attr("data-theme", newTheme);

        localStorage.setItem("theme", newTheme);

        // İkonu Güncelleme
        jQuery("#theme")
            .removeClass("bi bi-sun bi-moon")
            .addClass(newTheme === "dark" ? "bi bi-sun" : "bi bi-moon");
    });

  }

};
</script>

