<template>
  <div class="detailPage">
    <h2 class="titlePage mt-4 mb-5">Habit Detail</h2>
    <HabitItem v-if="selectedHabit" :habit="selectedHabit" :isEditing="true" />
    <p v-else>Loading...</p>
  </div>
</template>

<script>
import HabitItem from '@/components/HabitItem.vue';
import { ref, onMounted } from 'vue';
import { useRoute } from 'vue-router';
import { useHabitStore } from '../stores/habitStore';

export default {
  components: { HabitItem },
  setup() {
    const habitStore = useHabitStore();
    const route = useRoute();
    const selectedHabit = ref(null);

    // habitId parametresini sayıya dönüştürerek karşılaştırın
    onMounted(() => {
        habitStore.loadHabits();
        const habitId = Number(route.params.habitId); // ID'yi sayıya dönüştürün
        selectedHabit.value = habitStore.getHabitById(habitId);
        if (!selectedHabit.value) {
            console.error(`Habit with ID ${habitId} not found`);
        }
    });

    return {
      selectedHabit,
    };
  },
};
</script>
