// src/store/habitStore.js
import { defineStore } from 'pinia';

export const useHabitStore = defineStore('habitStore', {
    state: () => ({
        habits: JSON.parse(localStorage.getItem('habits')) || [], // Tüm alışkanlıkları JSON formatında kaydet
    }),
    actions: {
        getHabitById(id) {
            return this.habits.find(habit => habit.id == id); // Hem String hem de Number ID'lerle eşleşme
        },

        addHabit(habit) {
            this.habits.push(habit);
            this.saveHabits(); // Yeni bir alışkanlık eklendiğinde kaydet
        },
        deleteHabit(id) {
            this.habits = this.habits.filter(habit => habit.id !== id);
            this.saveHabits(); // Silindiğinde güncelle
        },
        updateHabit(updatedHabit) {
            const index = this.habits.findIndex(habit => habit.id === updatedHabit.id);
            if (index !== -1) {
                this.habits.splice(index, 1, updatedHabit); // Güncellenmiş alışkanlığı diziye kaydet
                this.saveHabits(); // Kaydet
            }
        },
        saveHabits() {
            // Tüm alışkanlıkları JSON formatında `localStorage`'a kaydet
            localStorage.setItem('habits', JSON.stringify(this.habits));
        },
        loadHabits() {
            // `localStorage`'dan tüm alışkanlıkları tekrar yükle
            this.habits = JSON.parse(localStorage.getItem('habits')) || [];
        },
        saveCheckedState(propsId, value) {
            // Checkbox durumunu localStorage'a kaydet
            localStorage.setItem(propsId, value);
        },
        loadCheckedState(propsId) {
            // localStorage'dan checkbox durumunu yükle
            const savedChecked = localStorage.getItem(propsId);
            return savedChecked === 'true'; // Değeri boolean olarak döndür
        },
    },
});
